  display: flex;
  flex: 0 0 auto;
  align-items: center;
  height: 5.75rem;
  margin-bottom: 1.875rem;
  padding: 0 2.5rem;
  font-family: Arial, sans-serif;
  border-bottom: 1px solid #f0f0f0;
}

