.header__menu {
  display: none;
  width: 3.5rem;
  height: 100%;
  margin: 0 0 0 auto;
  padding: 0;
  background: 50% 50% no-repeat transparent url(../assets/icon_list_m@1x.svg);
  border: none;
  border-radius: 0;
  appearance: none;
  cursor: pointer;
}

.header__links {
  display: flex;
  gap: 1.75rem;
  margin: 0;
  padding: 0;
  list-style: none;
}

@keyframes showMenu {
  from {
    transform: translateY(-1rem);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes hideMenu {
  from {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
  to {
    transform: translateY(-1rem);
    opacity: 0;
    visibility: hidden;
  }
}

.header__item {
  margin: 0;
  padding: 0;
}

.header__link {
  font-weight: bold;
  opacity: 0.6;
  transition: opacity 0.15s ease-in-out;
  text-decoration: none;
  color: #333333;
}

.header__link:hover {
  opacity: 1;
}

.header__link_current {
  opacity: 1;
}

.main {
  display: grid;
  flex: 0 0 auto;
  grid-template-columns: 50% 50%;
  gap: 1.625rem;
  padding: 0 2.5rem;
}

.main__devices {
  grid-column-start: 1;
  grid-column-end: 3;
}

.section__title-header {
  margin: 0;
  font-size: 1.125rem;
  font-weight: bold;
  color: #333333;
}

.section__title {
  display: flex;
  align-items: baseline;
  margin: 0 0 1.25rem 0;
}

.section__select {
  display: none;
  margin: 0 0 0 auto;
  padding: 0.4375rem 1.9375rem 0.5rem 1rem;
  border: none;
  border-radius: 1.25rem;
  font-size: 0.8125rem;
  font-family: inherit;
  font-weight: bold;
  background: no-repeat calc(100% - 1rem) 50% #ffd93e
    url(../assets/arrow-down.svg);
  appearance: none;
}

.section__tabs {
  display: flex;
  gap: 0.625rem;
  align-items: baseline;
  margin: 0 0 0 0.8125rem;
  padding: 0;
  list-style: none;
}

.section__tab {
  flex: 0 0 auto;
  padding: 0.4375rem 0.9375rem 0.5rem;
  font-size: 0.8125rem;
  font-weight: bold;
  background: #f7f7f7;
  border-radius: 1024px;
  cursor: pointer;
  transition: 0.25s ease-in-out;
  transition-property: background-color, box-shadow;
}

.section__tab_active {
  background: #ffd93e;
}

.section__tab:not(.section__tab_active):hover {
  background: #fff;
  box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.5);
}

.hero-dashboard {
  position: relative;
  box-sizing: border-box;
  display: flex;
  gap: 0.9375rem;
  height: 20.6875rem;
  /* background: no-repeat 50% 50% url(../assets/bg@2x.png); */
  background: no-repeat 50% 50% url(../assets/bg@2x.webp);
  /*background-size: cover;*/
  background-size: 1260px;
  border-radius: 1.25rem;
}

.hero-dashboard__primary {
  flex: 1 1 auto;
  padding: 1.25rem 0 1.25rem 1.25rem;
}

.hero-dashboard__title {
  margin: 0.375rem 0;
  font-size: 1.6875rem;
  font-weight: bold;
  color: #333333;
}

.hero-dashboard__subtitle {
  margin: 0.375rem 0 0 0;
  font-size: 0.875rem;
  color: #333333;
}

.hero-dashboard__info {
  display: flex;
  gap: 1.125rem;
  margin: 1.5625rem 0 0 0;
  padding: 0;
  list-style: none;
}

.hero-dashboard__item {
  margin: 0;
  padding: 0;
}

.hero-dashboard__item-title {
  font-size: 0.875rem;
  color: #858585;
}

.hero-dashboard__item-details {
  display: flex;
  align-items: center;
  margin-top: 0.25rem;
  font-size: 2.25rem;
  font-weight: bold;
  color: #333333;
}

.hero-dashboard__icon {
  width: 1.625rem;
  height: 1.625rem;
  margin-left: 0.3125rem;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
}

.hero-dashboard__icon_rain {
  background-image: url(../assets/cloud-drizzle.svg);
}

.hero-dashboard__schedule {
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 0.9375rem;
  margin: 0;
  padding: 1.25rem 1.5625rem 1.25rem 0;
  overflow: auto;
  list-style: none;
  scrollbar-width: none;
}

.hero-dashboard__schedule::-webkit-scrollbar {
  display: none;
}

.event__button {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: end;
  gap: 0.1875rem;
  width: 100%;
  min-width: 12.5rem;
  height: 7.5rem;
  margin: 0;
  padding: 0.875rem;
  border-radius: 1.25rem;
  border: none;
  background: #f7f7f7;
  text-align: left;
  transition: 0.25s ease-in-out;
  transition-property: background-color, box-shadow;
  cursor: pointer;
  appearance: none;
}

.event_slim .event__button {
  height: 6.25rem;
}

.event__button:hover {
  background: #fff;
  box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.5);
}

.event__icon {
  position: absolute;
  top: 0.875rem;
  left: 0.875rem;
  width: 1.5rem;
  height: 1.5rem;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}

.event__icon_temp {
  background-image: url(../assets/icon_temperature.svg);
}

.event__icon_temp2 {
  background-image: url(../assets/icon_temperature_2.svg);
}

.event__icon_light {
  background-image: url(../assets/icon_sun.svg);
}

.event__icon_light2 {
  background-image: url(../assets/icon_sun_2.svg);
}

.event__icon_schedule {
  background-image: url(../assets/icon_scheduled.svg);
}

.event__title {
  margin: 0;
  font-size: 0.875rem;
  font-weight: bold;
  color: #333333;
}

.event__subtitle {
  font-size: 0.75rem;
  color: #858585;
}

.event-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 0.9375rem;
  margin: 0;
  padding: 0;
  list-style: none;
}

.section__panel {
  padding-bottom: 0.625rem;
  overflow-x: auto;
  scrollbar-width: none;
}

.section__panel::-webkit-scrollbar {
  display: none;
}

.section__panel_hidden {
  display: none;
}

.section__panel-list {
  display: flex;
  gap: 0.9375rem;
  margin: 0;
  padding: 0;
  list-style: none;
}

.section__panel-wrapper {
  position: relative;
}

.section__arrow {
  position: absolute;
  top: 0;
  right: 0;
  width: 18px;
  bottom: 10px;
  background: no-repeat 50% 50% url(../assets/arrow-left.webp);
  transform: rotate(180deg);
  cursor: pointer;
}

.section__arrow:hover {
  opacity: 0.7;
}
